import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {fetcher} from "../content-api/api-util";

export function TOC() {

    const [titles, setTitles] = useState(null);
    useEffect(() => {
        (async function f1() {
            let slokas = await fetcher.slokas;
            setTitles(Object.values(slokas.title));
        })();
    }, []);

    const styles = {
        highlight: {color: "yellow"},
        linkStyle: {textDecoration: 'none', color: 'palegoldenrod', fontWeight: 200, display: "block"}
    }

    return <>
        {titles && <table align={"center"}
                          style={{textAlign: "left", fontSize: 20, lineHeight: 2}}>
            <tbody>
            <tr>
                <td>
                    <div style={{color: 'white'}}>
                        ఆడియో - కోసం <span style={styles.highlight}>శ్లోకం</span> మీద click చెయ్యండి <br/>
                        భావం - కోసం <span style={styles.highlight}>శ్లోకం number</span> మీద click చెయ్యండి <br/>
                        <Link to={`/stats`} style={{...styles.linkStyle, textAlign: "center", color: "yellow"}}>
                            → Videos, Imp Slokas, Stats ←
                        </Link>
                    </div>
                    {titles.map((title, i) =>
                        <Link key={i} to={`/${i + 1}`} style={styles.linkStyle}>{i + 1 + ". " + title}</Link>
                    )}
                    <span style={{fontSize: "0.9rem", color: 'palegoldenrod'}}>
                        ..created this webapp on 19<sup>th</sup> Dec 2021, <br/>
                        in the memory of my daughter <i>Advaita's</i> 6<sup>th</sup> Birthday..
                    </span>
                </td>
            </tr>
            </tbody>
        </table>
        }
    </>
}